<template>
  <div class="col-md-6 col-lg-4">
    <div class="card mb-4">
      <img
        class="card-img-top"
        :src="restaurant.image | emptyImage"
        alt="Card image cap"
        width="286px"
        height="180px"
      >
      <div class="card-body">
        <p class="card-text title-wrap">
          <router-link
            :to="{
              name:'restaurant',
              params: {id: restaurant.id}
            }"
          >
            {{restaurant.name}}
          </router-link>
        </p>
        <span class="badge badge-secondary">{{restaurant.Category? restaurant.Category.name : '未分類'}}</span>
        <p class="card-text text-truncate">
          {{restaurant.description}}
        </p>
      </div>
      <div class="card-footer">
        <button
          v-if="restaurant.isFavorited"
          @click.stop.prevent="removeFavorite(restaurant.id)"
          type="button"
          class="btn btn-danger btn-border favorite mr-2"
          :disabled="isProcessing"
        >
          移除最愛
        </button>
        <button
          v-else
          @click.stop.prevent="addFavorite(restaurant.id)"
          type="button"
          class="btn btn-primary btn-border favorite mr-2"
          :disabled="isProcessing"
        >
          加到最愛
        </button>
        <button
          v-if="restaurant.isLiked"
          @click.stop.prevent="disLike(restaurant.id)"
          type="button"
          class="btn btn-danger like mr-2"
          :disabled="isProcessing"
        >
          Unlike
        </button>
        <button
          v-else
          @click.stop.prevent="like(restaurant.id)"
          type="button"
          class="btn btn-primary like mr-2"
          :disabled="isProcessing"
        >
          Like
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import {EmptyImageFilter} from './../utils/mixins'
import usersAPI from './../apis/user'
import {Toast} from './../utils/helpers'
export default {
  name: 'RestaurantCard',
  mixins: [ EmptyImageFilter ],
  props: {
    initialRestaurant: {
      type: Object,
      required: true
    }
  },
  data () {
    return{
      restaurant: this.initialRestaurant,
      isProcessing: false
    }    
  },
  methods: {
    async addFavorite(restaurantId){
      try{   
        this.isProcessing = true    
        const {data} = await usersAPI.addFavorite({restaurantId})
        if(data.status !== 'success'){
          throw new Error(data.message)
        }
        this.restaurant = {
        ...this.restaurant, //保存餐廳內原有資料
        isFavorited: true //要覆蓋的屬性
        }
        this.isProcessing = false  
      }catch(error){
        this.isProcessing = false  
        Toast.fire({
          icon: 'warning',
          title: '無法新增我的最愛，請稍後再試'
        })
        console.log(error)
      }
    },
    async removeFavorite(restaurantId){
      try{
        this.isProcessing = true  
        console.log('page',restaurantId)
        const {data} = await usersAPI.removeFavorite(restaurantId)
        if(data.status !== 'success') {
          throw new Error (data.message)
        }
        this.restaurant = {
        ...this.restaurant, 
        isFavorited: false 
        }
        this.isProcessing = false  
      }catch(error){
        this.isProcessing = false  
        Toast.fire({
          icon: 'warning',
          title: '無法移除我的最愛，請稍後再試'
        })
      }
      
      
    },
    async like(restaurantId){
      try{
        this.isProcessing = true  
        const {data} = await usersAPI.addLike({restaurantId})
        if(data.status !== 'success'){
          throw new Error (data.message)
        }
        this.restaurant = {
        ...this.restaurant, 
        isLiked: true 
        }
       this.isProcessing = false  
      }catch(error){
        this.isProcessing = false  
        Toast.fire({
          icon: 'warning',
          title: '無法按讚，請稍後再試'
        })
      }      
    },   
    async disLike(restaurantId){
      try{
        this.isProcessing = true  
        const {data} = await usersAPI.disLike({restaurantId})
        if(data.status !== 'success'){
          throw new Error(data.message)
        }
        this.restaurant = {
        ...this.restaurant, 
        isLiked: false 
        }  
        this.isProcessing = false  
      }catch(error){
        this.isProcessing = false  
        Toast.fire({
          icon:'warning',
          title: '無法移除，請稍後再試'
        })
      }     
    }
  }            
}
</script>

<style scoped>
.badge.badge-secondary {
  padding: 0;
  margin: 8px 0;
  color: #bd2333;
  background-color: transparent;
}

.btn,
.btn-border.btn:hover {
  margin: 7px 14px 7px 0;
}

.card {
  margin-bottom: 2rem !important;
}
.card-img-top {
  background-color: #EFEFEF;
}

.card-body {
  padding: 17.5px;
}

.card-footer {
  padding: 9px 17.5px;
  border-color: rgb(232, 232, 232);
  background: white;
}
</style>